var option = {
    animation: true,
    delay: 2000
};

function ShowNotification() {
    var toastHTMLElement = document.getElementById("notification");
    var toastElement = new bootstrap.Toast(toastHTMLElement, option);
    toastElement.show();
}

var modal = new bootstrap.Modal(document.querySelector('#modaldescription'));
var modalBody = modal._element.querySelector('.modal-body');
var descriptions = ['–≠—Ç–æ –ê—Ä–∞—Ö–∏—Å. –£ –Ω–µ–µ –≤—Å–µ –Ω–æ—Ä–º–∞–ª—å–Ω–æ —Å –Ω–æ–≥–∞–º–∏, –ø—Ä–æ—Å—Ç–æ –æ–Ω–∞ –æ—á–µ–Ω—å —Ç–∞–ª–∞–Ω—Ç–ª–∏–≤–æ –º–æ–∂–µ—Ç —Å–µ—Å—Ç—å –Ω–∞ —à–ø–∞–≥–∞—Ç. ', 
'–≠—Ç–æ –ì—É—Å—å. –¢—Ä—É–¥–Ω–æ –±—ã—Ç—å —Ç–∞–∫–∏–º –º–∞–ª–µ–Ω—å–∫–∏–º –∏ —Ç–∞–∫–∏–º –∫—Ä—É–≥–ª—ã–º, –Ω–æ –∫—Ç–æ-—Ç–æ –∂–µ –¥–æ–ª–∂–µ–Ω —ç—Ç–æ –¥–µ–ª–∞—Ç—å. –ò —ç—Ç–æ—Ç –∫—Ç–æ-—Ç–æ - –ì—É—Å—å.', 
'–≠—Ç–æ –ù–æ–Ω –∏ –ê—Å–∞. –ù–æ–Ω —Å–ª—É—á–∞–π–Ω–æ –Ω–∞–∂–∞–ª –∫–Ω–æ–ø–∫—É, –∫–æ—Ç–æ—Ä–∞—è –∞–∫—Ç–∏–≤–∏—Ä—É–µ—Ç —Ç—É—Ä–±–æ-—Ä–µ–∂–∏–º —É –ê—Å—ã.–ù–µ–º–µ–¥–ª–µ–Ω–Ω–æ –±–µ–≥–∏—Ç–µ –æ—Ç—Å—é–¥–∞', 
'–°–µ—Ä—å–µ–∑–Ω–æ? –ï—â–µ –æ–¥–Ω–æ —Ñ–æ—Ç–æ –±–µ–∑ —Å–æ–±–∞–∫–∏? –ù–µ –º–æ–≥—É –ø–æ–≤–µ—Ä–∏—Ç—å, —á—Ç–æ –≤—ã –ø—Ä–æ—Å—Ç–æ –ø—Ä–∏—Å–ª–∞–ª–∏ —Ñ–æ—Ç–æ –¥–µ—Ä–µ–≤–∞...',
 '–≠—Ç–æ –ì–µ—Ä–∞–∫–ª. –û–Ω —Ç–æ–ª—å–∫–æ —á—Ç–æ –ø–µ—Ä–µ–¥–≤–∏–Ω—É–ª —Å–≤–æ–µ–≥–æ —Ñ–µ—Ä–∑—è –Ω–∞ B5, –Ω–µ –æ—Å—Ç–∞–≤–∏–≤ –≤–∞—à–µ–º—É –∫–æ—Ä–æ–ª—é —Å–ø–∞—Å–µ–Ω–∏—è.'];
var currentDescriptionIndex = 0;

function ShowModal(button) {
    currentDescriptionIndex = button.dataset.description;
    var description = descriptions[button.dataset.description];
    modalBody.textContent = description;
    modal.show();
}

function showNextDescription() {
    currentDescriptionIndex = (currentDescriptionIndex + 1) % descriptions.length;
    modalBody.textContent = descriptions[currentDescriptionIndex];
}

function showPreviousDescription() {
    currentDescriptionIndex = (currentDescriptionIndex - 1 + descriptions.length) % descriptions.length;
    modalBody.textContent = descriptions[currentDescriptionIndex];
}

document.addEventListener('keydown', function(event) {
    if (event.code === 'ArrowRight') {
        showNextDescription();
    } else if (event.code === 'ArrowLeft') {
        showPreviousDescription();
    }
});

var popoverTriggerList = Array.prototype.slice.call(document.querySelectorAll('[data-bs-toggle="popover"]'))
var popoverList = popoverTriggerList.map(function (popoverTriggerEl) {
  return new bootstrap.Popover(popoverTriggerEl)
})